// src/index.ts
import './style.css';
import { ClocksManager } from './clocksManager';
document.addEventListener('DOMContentLoaded', function () {
    console.log('DOM fully loaded and parsed');
    var clocksContainer = document.querySelector('.clocks');
    var addClockButton = document.getElementById('add-clock-button');
    var timezoneSelector = document.getElementById('timezone-selector');
    if (!clocksContainer || !addClockButton || !timezoneSelector) {
        console.error('Clocks container, add clock button or timezone selector element was not found.');
        return;
    }
    // Check if ClocksManager instance already exists to prevent multiple initializations
    if (!window.clocksManagerInitialized) {
        var clocksManager_1 = new ClocksManager(clocksContainer);
        addClockButton.addEventListener('click', function () {
            console.log('Add Clock button clicked');
            var selectedTimeZoneOffset = parseInt(timezoneSelector.value);
            clocksManager_1.addClock(selectedTimeZoneOffset);
        });
        // Set a flag to indicate that the ClocksManager has been initialized
        window.clocksManagerInitialized = true;
    }
    else {
        console.log('ClocksManager already initialized');
    }
});
