import { Watch } from './watch';
var ClocksManager = /** @class */ (function () {
    function ClocksManager(rootElement) {
        this.rootElement = rootElement;
        this.watches = [];
        this.initializeClocks();
    }
    ClocksManager.prototype.initializeClocks = function () {
        var _this = this;
        var clockElements = this.rootElement.querySelectorAll('.circle');
        clockElements.forEach(function (clockElement) {
            _this.createWatch(clockElement, 0); // Utiliser GMT+0 par défaut pour les horloges initiales
        });
    };
    ClocksManager.prototype.createWatch = function (clockElement, timeZoneOffset) {
        var timeElement = clockElement.querySelector('.time');
        var modeButton = clockElement.querySelector('.mode');
        var increaseButton = clockElement.querySelector('.increase');
        var lightButton = clockElement.querySelector('.light');
        var resetButton = clockElement.querySelector('.reset');
        var toggleFormatButton = clockElement.querySelector('.toggle-format');
        if (timeElement && modeButton && increaseButton && lightButton && resetButton && toggleFormatButton) {
            var watch = new Watch(timeElement, modeButton, increaseButton, lightButton, resetButton, toggleFormatButton, timeZoneOffset);
            this.watches.push(watch);
        }
        else {
            console.error('One or more required elements were not found in a clock.');
        }
    };
    ClocksManager.prototype.addClock = function (timeZoneOffset) {
        var clockElement = document.createElement('div');
        clockElement.classList.add('circle');
        clockElement.innerHTML = "\n            <div class=\"watch\">\n                <div class=\"screen\">\n                    <div class=\"time\">00:00<span class=\"seconds\">00</span></div>\n                    <div class=\"button mode\">mode</div>\n                    <div class=\"button increase\">increase</div>\n                    <div class=\"button light\">light</div>\n                    <div class=\"button reset\">reset</div>\n                    <div class=\"button toggle-format\"></div>\n                </div>\n            </div>\n        ";
        this.rootElement.appendChild(clockElement);
        this.createWatch(clockElement, timeZoneOffset);
    };
    return ClocksManager;
}());
export { ClocksManager };
