import { Time } from './time';
var Watch = /** @class */ (function () {
    function Watch(timeElement, modeButton, increaseButton, lightButton, resetButton, toggleFormatButton, timeZoneOffset) {
        this.timeElement = timeElement;
        this.modeButton = modeButton;
        this.increaseButton = increaseButton;
        this.lightButton = lightButton;
        this.resetButton = resetButton;
        this.toggleFormatButton = toggleFormatButton;
        this.timeZoneOffset = timeZoneOffset;
        this.editMode = 'none';
        this.isNightMode = false;
        this.is24HourFormat = true;
        this.time = new Time();
        this.modeButton.addEventListener('click', this.switchEditMode.bind(this));
        this.increaseButton.addEventListener('click', this.increaseTime.bind(this));
        this.lightButton.addEventListener('click', this.switchToLightMode.bind(this));
        this.resetButton.addEventListener('click', this.resetTime.bind(this));
        this.toggleFormatButton.addEventListener('click', this.toggleTimeFormat.bind(this));
        this.start();
    }
    Watch.prototype.start = function () {
        var _this = this;
        this.updateClockDisplay();
        this.timer = window.setInterval(function () {
            _this.time.incrementSeconds();
            _this.updateClockDisplay();
        }, 1000);
    };
    Watch.prototype.switchEditMode = function () {
        switch (this.editMode) {
            case 'none':
                this.editMode = 'hours';
                break;
            case 'hours':
                this.editMode = 'minutes';
                break;
            case 'minutes':
                this.editMode = 'none';
                break;
        }
        console.log('Edit mode:', this.editMode);
    };
    Watch.prototype.increaseTime = function () {
        if (this.editMode === 'hours') {
            this.time.addHour();
        }
        else if (this.editMode === 'minutes') {
            this.time.addMinute();
        }
        this.updateClockDisplay();
    };
    Watch.prototype.switchToLightMode = function () {
        this.isNightMode = !this.isNightMode;
        document.body.classList.toggle('night-mode', this.isNightMode);
    };
    Watch.prototype.resetTime = function () {
        this.time = new Time(); // Réinitialiser l'heure à 00:00:00 dans le fuseau horaire spécifié
        this.updateClockDisplay();
    };
    Watch.prototype.toggleTimeFormat = function () {
        this.is24HourFormat = !this.is24HourFormat;
        this.updateClockDisplay();
    };
    Watch.prototype.updateClockDisplay = function () {
        var updatedTime = this.time.getTime();
        var hours = updatedTime.getHours();
        var minutes = updatedTime.getMinutes().toString().padStart(2, '0');
        var seconds = updatedTime.getSeconds().toString().padStart(2, '0');
        var period = '';
        if (!this.is24HourFormat) {
            period = hours >= 12 ? 'PM' : 'AM';
            hours = hours % 12;
            hours = hours ? hours : 12; // l'heure '0' doit être '12'
        }
        var formattedHours = hours.toString().padStart(2, '0');
        this.timeElement.innerHTML = "".concat(formattedHours, ":").concat(minutes, "<span class=\"seconds\">").concat(seconds, "</span> <span class=\"ampm\">").concat(period, "</span>");
    };
    return Watch;
}());
export { Watch };
